import apiClient from './client'
import type { PaginatedResponse } from '../types/api'
import type { 
  Route, 
  Station, 
  Vehicle,
  TransitAnalytics,
  RouteFilters,
  CreateRouteRequest,
  UpdateRouteRequest,
  GPSLocation
} from '../types/transit'

export const transitApi = {
  getRoutes: async (filters?: RouteFilters): Promise<PaginatedResponse<Route>> => {
    const response = await apiClient.get('/transit/routes', { params: filters })
    return response.data
  },

  getRoute: async (id: string): Promise<Route> => {
    const response = await apiClient.get(`/transit/routes/${id}`)
    return response.data
  },

  createRoute: async (data: CreateRouteRequest): Promise<Route> => {
    const response = await apiClient.post('/transit/routes', data)
    return response.data
  },

  updateRoute: async (id: string, data: UpdateRouteRequest): Promise<Route> => {
    const response = await apiClient.patch(`/transit/routes/${id}`, data)
    return response.data
  },

  deleteRoute: async (id: string): Promise<void> => {
    await apiClient.delete(`/transit/routes/${id}`)
  },

  getStations: async (): Promise<PaginatedResponse<Station>> => {
    const response = await apiClient.get('/transit/stations')
    return response.data
  },

  createStation: async (data: Omit<Station, 'id' | 'createdAt' | 'updatedAt'>): Promise<Station> => {
    const response = await apiClient.post('/transit/stations', data)
    return response.data
  },

  updateStation: async (id: string, data: Partial<Station>): Promise<Station> => {
    const response = await apiClient.patch(`/transit/stations/${id}`, data)
    return response.data
  },

  deleteStation: async (id: string): Promise<void> => {
    await apiClient.delete(`/transit/stations/${id}`)
  },

  getVehicles: async (): Promise<PaginatedResponse<Vehicle>> => {
    const response = await apiClient.get('/transit/vehicles')
    return response.data
  },

  createVehicle: async (data: Omit<Vehicle, 'id' | 'createdAt'>): Promise<Vehicle> => {
    const response = await apiClient.post('/transit/vehicles', data)
    return response.data
  },

  updateVehicle: async (id: string, data: Partial<Vehicle>): Promise<Vehicle> => {
    const response = await apiClient.patch(`/transit/vehicles/${id}`, data)
    return response.data
  },

  deleteVehicle: async (id: string): Promise<void> => {
    await apiClient.delete(`/transit/vehicles/${id}`)
  },

  getVehicleLocation: async (vehicleId: string): Promise<GPSLocation> => {
    const response = await apiClient.get(`/transit/vehicles/${vehicleId}/location`)
    return response.data
  },

  getVehicleLocations: async (): Promise<{ vehicleId: string; location: GPSLocation }[]> => {
    const response = await apiClient.get('/transit/vehicles/locations')
    return response.data
  },

  getRouteAnalytics: async (routeId: string, date: string): Promise<TransitAnalytics> => {
    const response = await apiClient.get(`/transit/routes/${routeId}/analytics`, { params: { date } })
    return response.data
  },

  optimizeRoute: async (routeId: string): Promise<Route> => {
    const response = await apiClient.post(`/transit/routes/${routeId}/optimize`)
    return response.data
  },
}
