export const useVehicleLocations = () => {
  return useQuery<VehicleLocation[]>({
    queryKey: [TRANSIT_QUERY_KEY, VEHICLES_QUERY_KEY, 'locations'],
    queryFn: async () => {
      if (isDemoMode()) {
        return Array.from({ length: 12 }).map((_, i) => ({
          vehicleId: `vehicle-${i + 1}`,
          location: {
            latitude: 31.2304 + (Math.random() - 0.5) * 0.1,
            longitude: 121.4737 + (Math.random() - 0.5) * 0.1,
            speed: Math.floor(Math.random() * 60 + 10),
            heading: Math.floor(Math.random() * 360)
          },
          timestamp: new Date().toISOString()
        }))
      }
      const response = await transitApi.getVehicleLocations()
      // Handle both { data: [...] } and direct array response formats
      return Array.isArray(response) ? response : (response as any)?.data || []
    },
    refetchInterval: isDemoMode() ? 5000 : 30000,
  })
}

export const useRouteAnalytics = (routeId: string, date: string) => {
  return useQuery({
    queryKey: [TRANSIT_QUERY_KEY, ROUTES_QUERY_KEY, routeId, 'analytics', date],
    queryFn: () => transitApi.getRouteAnalytics(routeId, date),
    enabled: !!routeId && !!date,
  })
}

export const useExchangeRates = () => {
  return useQuery({
    queryKey: [TRANSIT_QUERY_KEY, 'exchangeRates'],
    queryFn: async () => {
      // Mock exchange rates vs CNY
      const baseRates = {
        USD: 7.12,
        JPY: 0.048,
        EUR: 7.75,
        GBP: 9.05,
        KRW: 0.0053,
        THB: 0.19,
        HKD: 0.91,
        SGD: 5.31,
        MYR: 1.51,
        TWD: 0.22,
        PHP: 0.13,
      }

      // Add small random fluctuation
      const rates = Object.entries(baseRates).reduce((acc, [curr, rate]) => {
        acc[curr] = +(rate * (1 + (Math.random() - 0.5) * 0.01)).toFixed(4)
        return acc
      }, {} as Record<string, number>)

      return {
        base: 'CNY',
        timestamp: new Date().toISOString(),
        rates,
      }
    },
    refetchInterval: 300000, // 5 minutes as per spec
  })
}
export const useKyc = () => {
  return useMutation({
    mutationFn: async (step: 'UPLOAD' | 'OCR' | 'FACE_MATCH' | 'VERIFY') => {
      // Simulate processing time
      await new Promise(resolve => setTimeout(resolve, 1500))

      if (step === 'VERIFY') {
        return { success: true, status: 'VERIFIED', score: 98 }
      }

      return { success: true, nextStep: true }
    }
  })
}
